/*
app variables
*/

:root {
  /* background colors */
  --action-key-color: #278b9c;
  --action-key-color-hover: #0d414b;
  --calculator-background-color: #278b9c;
  --display-background-color: #1d1f1f;
  --number-key-color: #1e6a77;
  --number-key-color-hover: #0d414b;
  --submit-key-color: #037562;
  --submit-key-color-hover: #00473c;
  text-transform: uppercase;

  /* font */
  --main-font: 'Poppins', sans-serif;

  /* font colors */
  --display-text-color: #08fdf1;
  --key-text-color: #d3d3d3;

  /* font sizes */
  --display-text-size: 3em;
  --key-text-size: 2em;

  /* font weights */
  --display-text-weight: 400;
  --key-text-weight: 700;

  /* calculator dimensions */
  --calculator-height: 82%;
  --calculator-width: 40%;

  /* display dimensions */
  --display-height: 24%;
  --display-width: 92%;

  /* keypad dimensions */
  --keypad-height: 72%;
  --keypad-width: 96%;
}

@media screen and (max-width: 1024px) {
  :root {
    /* font sizes */
    --display-text-size: 6em;
    --key-text-size: 3em;

    /* calculator dimensions */
    --calculator-height: 100%;
    --calculator-width: 100%;
  }
}

/*
app CSS reset  
*/

body,
div,
p {
  margin: 0;
  padding: 0;
}
